'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { 
  Shield, 
  Sparkles, 
  Zap, 
  User, 
  Lock, 
  Globe, 
  ArrowRight, 
  AlertCircle 
} from 'lucide-react';
import { MockAuthService } from '@/lib/auth/mock-auth-service';
import { useNotifications } from '@/lib/store/ui-store';

/**
 * 登录页面组件
 */
export default function LoginPage() {
  const router = useRouter();
  const { success, error: showError } = useNotifications();

  // 状态管理
  const [formData, setFormData] = useState({
    username: '',
    password: '',
    tenantCode: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [showTenantField, setShowTenantField] = useState(false);

  // 表单验证
  const validateForm = () => {
    if (!formData.username.trim()) {
      showError('验证失败', '请输入用户名');
      return false;
    }
    
    if (!formData.password.trim()) {
      showError('验证失败', '请输入密码');
      return false;
    }
    
    if (formData.password.length < 6) {
      showError('验证失败', '密码长度至少6位');
      return false;
    }
    
    return true;
  };

  // 处理登录提交
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!formData.username || !formData.password) {
      setErrorMessage('请输入用户名和密码');
      return;
    }

    setIsLoading(true);
    setErrorMessage('');

    try {
      // 使用模拟认证服务进行登录
      await MockAuthService.login({
        username: formData.username,
        password: formData.password
      });

      success('登录成功', '欢迎回来！');
      // 登录成功，跳转到首页
      router.push('/');
    } catch (err) {
      const errorMsg = err instanceof Error ? err.message : '登录失败，请重试';
      setErrorMessage(errorMsg);
      showError('登录失败', errorMsg);
    } finally {
      setIsLoading(false);
    }
  };

  // 处理输入变化
  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
    // 清除错误信息
    if (errorMessage) {
      setErrorMessage('');
    }
  };

  return (
    <div className="min-h-screen flex">
      {/* 左侧品牌区域 */}
      <div className="hidden lg:flex lg:w-1/2 bg-gradient-to-br from-blue-600 via-blue-700 to-indigo-800 relative overflow-hidden">
        {/* 背景装饰 */}
        <div className="absolute inset-0 bg-black/10"></div>
        <div className="absolute top-0 left-0 w-full h-full">
          <div className="absolute top-20 left-20 w-32 h-32 bg-white/10 rounded-full blur-xl"></div>
          <div className="absolute bottom-20 right-20 w-48 h-48 bg-white/5 rounded-full blur-2xl"></div>
        </div>
        
        {/* 内容 */}
        <div className="relative z-10 flex flex-col justify-center px-12 text-white">
          <div className="mb-12">
            <div className="flex items-center mb-4">
              <Shield className="w-8 h-8 mr-3" />
              <div>
                <h1 className="text-2xl font-bold">ITSM 系统</h1>
                <p className="text-blue-100 text-sm">智能IT服务管理平台</p>
              </div>
            </div>
          </div>
          
          {/* 特性标签 */}
          <div className="space-y-8">
            <div className="flex items-start">
              <div className="flex-shrink-0 w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center mr-4">
                <Sparkles className="w-6 h-6" />
              </div>
              <div>
                <h3 className="font-semibold text-lg">智能工单管理</h3>
                <p className="text-blue-100 text-sm">AI驱动的工单分类与自动化处理</p>
              </div>
            </div>
            
            <div className="flex items-start">
              <div className="flex-shrink-0 w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center mr-4">
                <Zap className="w-6 h-6" />
              </div>
              <div>
                <h3 className="font-semibold text-lg">实时监控告警</h3>
                <p className="text-blue-100 text-sm">7x24小时系统状态监控与预警</p>
              </div>
            </div>
            
            <div className="flex items-start">
              <div className="flex-shrink-0 w-12 h-12 bg-white/20 rounded-lg flex items-center justify-center mr-4">
                <Shield className="w-6 h-6" />
              </div>
              <div>
                <h3 className="font-semibold text-lg">多租户架构</h3>
                <p className="text-blue-100 text-sm">企业级安全与数据隔离保障</p>
              </div>
            </div>
          </div>
          
          {/* 底部文字 */}
          <div className="mt-12">
            <p className="text-sm">© 2024 ITSM System. 专业的IT服务管理解决方案</p>
          </div>
        </div>
      </div>

      {/* 右侧登录表单区域 */}
      <div className="flex-1 flex flex-col justify-center px-4 sm:px-6 lg:px-20 xl:px-24">
        {/* 移动端Logo */}
        <div className="lg:hidden text-center mb-8">
          <div className="flex items-center justify-center mb-4">
            <Shield className="w-8 h-8 mr-3 text-blue-600" />
            <div>
              <h1 className="text-2xl font-bold text-gray-900">ITSM 系统</h1>
              <p className="text-gray-500 text-sm">智能IT服务管理平台</p>
            </div>
          </div>
        </div>
        
        {/* 登录表单 */}
        <div className="w-full max-w-sm mx-auto">
          <div className="text-center mb-8">
            <h2 className="text-3xl font-bold text-gray-900 mb-2">系统登录</h2>
            <p className="text-gray-500">请输入您的账户信息</p>
          </div>

          {/* 错误提示 */}
          {errorMessage && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center">
              <AlertCircle className="w-5 h-5 text-red-500 mr-3 flex-shrink-0" />
              <span className="text-red-700 text-sm">{errorMessage}</span>
            </div>
          )}

          <form onSubmit={handleSubmit} className="space-y-6">
            {/* 用户名输入 */}
            <div>
              <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-2">
                用户名
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <User className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="username"
                  type="text"
                  value={formData.username}
                  onChange={(e) => handleInputChange('username', e.target.value)}
                  className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
                  placeholder="请输入用户名"
                  required
                />
              </div>
            </div>

            {/* 密码输入 */}
            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
                密码
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="password"
                  type="password"
                  value={formData.password}
                  onChange={(e) => handleInputChange('password', e.target.value)}
                  className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
                  placeholder="请输入密码"
                  required
                />
              </div>
            </div>

            {/* 租户代码输入（可选） */}
            {showTenantField && (
              <div className="transition-all duration-300 ease-out">
                <label htmlFor="tenantCode" className="block text-sm font-medium text-gray-700 mb-2">
                  租户代码
                </label>
                <div className="relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <Globe className="h-5 w-5 text-gray-400" />
                  </div>
                  <input
                    id="tenantCode"
                    type="text"
                    value={formData.tenantCode}
                    onChange={(e) => handleInputChange('tenantCode', e.target.value)}
                    className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors"
                    placeholder="请输入租户代码（可选）"
                  />
                </div>
              </div>
            )}

            {/* 登录按钮 */}
            <button
              type="submit"
              disabled={isLoading}
              className="w-full flex justify-center items-center px-4 py-3 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              {isLoading ? (
                <>
                  <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  <span>登录中...</span>
                </>
              ) : (
                <>
                  <span>登录</span>
                  <ArrowRight className="w-5 h-5" />
                </>
              )}
            </button>

            {/* 租户切换按钮 */}
            <div className="text-center">
              <button
                type="button"
                onClick={() => setShowTenantField(!showTenantField)}
                className="text-sm text-blue-600 hover:text-blue-700 transition-colors"
              >
                {showTenantField ? '隐藏租户设置' : '企业用户登录'}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
